.loader-div {
    position: fixed;
    top: 0;
    left: 0;
    background-color: #1b3d4275;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loader {
	position: relative;
	width: 10vw;
	height: 5vw;
	padding: 1.5vw;
	display: flex;
	align-items: center;
	justify-content: center;
}

.loader span {
    position: absolute;
    height: 4vw;
    width: 4vw;
    border-radius: 50%;
    background-color: #e91e63;
}

.loader span:nth-child(1) {
	-webkit-animation: loading-dotsA 0.5s infinite linear;
	        animation: loading-dotsA 0.5s infinite linear;
}

.loader span:nth-child(2) {
	-webkit-animation: loading-dotsB 0.5s infinite linear;
	        animation: loading-dotsB 0.5s infinite linear;
}

@-webkit-keyframes loading-dotsA {
	0% {
		transform: none;
        background-color: #3d1ee9;
	}
	25% {
		transform: translateX(2vw);

	}
	50% {
		transform: none;
        background-color: #e94d1e;
	}
	75% {
		transform: translateY(2vw);
	}
	100% {
		transform: none;
	}
}

@keyframes loading-dotsA {
	0% {
		transform: none;
        background-color: #3d1ee9;
	}
	25% {
		transform: translateX(2vw);

	}
	50% {
		transform: none;
        background-color: #e94d1e;
	}
	75% {
		transform: translateY(2vw);
	}
	100% {
		transform: none;
	}
}

@-webkit-keyframes loading-dotsB {
	0% {
		transform: none;
	}
	25% {
		transform: translateX(-2vw);
        background-color: #3d1ee9;
	}
	50% {
		transform: none;
	}
	75% {
		transform: translateY(-2vw);
        background-color: #e94d1e;
	}
	100% {
		transform: none;
	}
}

@keyframes loading-dotsB {
	0% {
		transform: none;
	}
	25% {
		transform: translateX(-2vw);
        background-color: #3d1ee9;
	}
	50% {
		transform: none;
	}
	75% {
		transform: translateY(-2vw);
        background-color: #e94d1e;
	}
	100% {
		transform: none;
	}
}

