:root {
    --color-text1: #3f4146;
    --color-text2: #ffffff;
    --color-btn: #565656;
    --card1-gradient-color1: #d952a2;
    --card1-gradient-color2: #aa1a90;
    --card2-gradient-color1: #fad62b;
    --card2-gradient-color2: #fcb30c;
    --card3-gradient-color1: #f89e00;
    --card3-gradient-color2: #f2540f;
  }
  .card-wrap {
    width: 90%;
    height: 375px;
    border-radius: 10px;
    position: relative;
    cursor: pointer;
}
  .card-wrap.one::after {
    content: "$100-$900";
    position: absolute;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    top: -55px;
    right: 0;
    z-index: 10;
    color: #ffffff;
    color: var(--color-text2);
    font-weight: 700;
    box-shadow: rgb(0 0 0 / 25%) 0px 14px 28px -10px, rgb(0 0 0 / 22%) 0px 10px 10px -5px;
